<template>
  <f7-page name="vueCalendar">
    
    <f7-navbar sliding>
      <f7-nav-left>
        <f7-link icon-f7="icon-bars" panel-open="left"></f7-link>
      </f7-nav-left>
      <f7-nav-title>
        Vue Calendar
      </f7-nav-title>
    </f7-navbar>
    
    <!-- Scrollable page content-->
    <f7-block inner>
      <fullCalendar :events="events"></fullCalendar>
    </f7-block>

  <!-- TOOLBAR -->
    <f7-toolbar bottom-md>
      <f7-link href="/">
        <f7-icon f7="home"></f7-icon>
      </f7-link>
      <f7-link href="/services/">
        <f7-icon f7="search"></f7-icon>
      </f7-link>
      <f7-link href="/report/list/">
        <f7-icon f7="calendar"></f7-icon>
      </f7-link>
      <f7-link>
        <f7-icon f7="camera"></f7-icon>
      </f7-link>
    </f7-toolbar>
  </f7-page>
</template>

<script>
import fullCalendar from 'vue-fullcalendar'
var demoEvents = [
        {
          title : 'Sunny Out of Office',
          start : '2018-07-05',
          end : '2018-07-06'
        },
        {
          title : 'NAMJU',
          start : '2018-07-02',
        },
    ]
export default {
    data(){
        return {
            events: demoEvents
        }
    },
    components: {
        fullCalendar,
    }
}
</script>


